import React, {Component} from 'react';

class ProjectData extends Component{

  render() {
    return(
        <div className='ProjectData'>
            <p>
                <b>Project Name</b>: {this.props.project.Title} <br/>
                <b>Completion</b>: {this.props.project.Completion} <br/>
                <b>Target Date</b>: {this.props.project.TargetDate} <br/>
                <b>Manager</b>: {this.props.project.Manager} <br/>
                <b>Overall Status</b>: {this.props.project.OverallStatus} <br/>
            </p>
            <hr/>
        </div>
        
    );
  }
}


export default ProjectData;
