import React, {Component} from 'react';
import ProjectData from './projectdata';

class Homepage extends Component{

  constructor(){
    super();
    this.state = { data: [], loadFlag: false };
  }

  componentDidMount() {
    fetch(`https://getprojectdetails.azurewebsites.net/api/GetProjectDetails`)
    .then(res => res.json())
    .then(json => {
      this.setState({data: json.data, loadFlag: true});
    })
    .catch(error => console.error('API-ERROR:' + error.toString()));
  }

  render() {
    if(this.state.loadFlag){
      return(
        <div className='container-fluid'>
          {
            this.state.data.map(project => {
              return(
                <div key={project.Title}>
                  <ProjectData project={project}/>
                </div>
              )
            })
          }
        </div>
      );
    } else {
      return(
        <div>
          FETCHING DATA...
        </div>
      );
    }
    
  }
}
//Created comment

export default Homepage;
